@import '../../../node_modules/slick-carousel/slick/slick.scss';


.carousel {
  margin: 0 auto;
  padding: 0 50px 30px;
  width: 650px;
  height: auto;

  &__item {
    margin: 0 auto;
  }

  &__img {
    width: 100%;
    height: 100%;
    object-fit: contain;
  }
}

// Кнопки навигации
.slick-arrow {
  position: absolute;
  top: 45%;
  transform: translateY(-50%);
  border: none;
  background-color: none;
  padding: 0;
  cursor: pointer;
  font-size: 0;
  background-color: transparent;

  &::before {
    display: table;
    font-size: 50px;
    line-height: 1;
    background-color: transparent;
    color: $light-theme__footer-bg;
  }

  &:hover,
  &:focus {
    &::before {
      color: $hover;
    }
  }
}

.slick-prev {
  left: 20px;

  &::before {
    content: "❮";
  }
}

.slick-next {
  right: 20px;

  &::before {
    content: "❯";
  }
}

// Пагинация
.slick-dots {
  list-style: none;
  padding: 0;
  margin: 0;
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);

  li {
    display: inline-block;
    margin: 0 5px;
  }

  button {
    padding: 0;
    border: none;
    background-color: $light-theme__footer-bg;
    cursor: pointer;
    border-radius: 50%;
    width: 12px;
    height: 12px;
    font-size: 0;

    &:hover,
    &:focus {
      opacity: 0.7;
    }
  }
}

.slick-active {
  button {
    background-color: $hover;
  }
}

.slick-slide {
  margin: 0 5px;
}

// Небольшой адаптив для слайдера
@media all and (max-width: 767px) {
  .carousel {
    max-width: 370px;
  }

  .slick-dots button {
    width: 8px;
    height: 8px;
  }

  .slick-arrow::before {
    font-size: 30px;
  }
}

@media all and (max-width: 415px) {
  .carousel {
    max-width: 280px;
  }
}