/* H E A D E R */
.header {
  background-color: $light-theme__header-bg;
  color: #ffffff;
  margin-bottom: 40px;
  font-weight: 500;
  font-size: 2rem;

  &__container {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__item--current {
    position: relative;
  }

  &__item--current::after {
    content: '';
    position: absolute;
    height: 2px;
    background-color: $hover;
    bottom: 0;
    left: 0;
    right: 0;
  }

  &__list {
    display: flex;
    flex-wrap: wrap;
    column-gap: 35px;
  }

  &__link {
    display: inline-block;
    color: inherit;
    text-decoration: none;
    padding: 20px 5px;
  }

  &__link.header__link--current {
    display: inline-block;
    color: inherit;
    text-decoration: none;
    // padding: 25px 5px;
    cursor: default;
  }

  &__link:hover,
  &__link:active,
  &__link:focus {
    background-color: $hover;
    opacity: 0.7;
    color: $light-theme__main-text;
  }

  &__logo {
    display: inline-block;
    background-color: transparent;
    border-radius: 50%;
    cursor: pointer;
    align-self: flex-start;
    margin: 5px 0;
    opacity: 0.8;
    box-shadow: 0 0 10px 0 #212121,
      0 0 100px 0 #212121 inset;
  }

  &__logo:hover,
  &__logo:active,
  &__logo:focus {
    opacity: 1;
  }

}

@media all and (max-width: 767px) {
  .header {
    font-size: 1.1rem;

    &__list {
      column-gap: 15px;
    }

    &__link {
      padding: 10px 5px;
    }
    &__logo {
      align-self: center;
    }
    &__logo-img {
      width: 32px;
      height: 25px;
    }
  }
}

@media all and (max-width: 416px) {
  .header {
    &__link {
      padding: 10px 5px;
    }
    &__logo {
      align-self: center;
    }
    &__logo-img {
      width: 50px;
      height: 43px;
    }
  }
}