.works {
display: grid;
grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
gap: 20px;

  &__heading {
    margin-bottom: 30px;
    margin-top: 20px;
    color: #757575;
    font-size: 2.8rem;
    line-height: 1.2;
  }

  &__list {
    display: flex;
    column-gap: 25px;
    flex-wrap: wrap;
  }

  &__item {
    margin-bottom: 10px;
    /* width: 200px; */
  }

  &__link {
    display: inline-block;
    position: relative;
    border: 2px solid #757575;
    padding: 5px 10px;
    border-radius: 5px;
    opacity: 0.7;
  }

  &__link:hover {
    opacity: 1;
  }
  @media all and (max-width: 767px) {
  
    .works__heading {
    font-size: 1.8rem;
    line-height: 1.2;
  }
  .works__list {
    column-gap: 15px;
  }
  .works__link {
    font-size: 1.3rem;
    line-height: 1.2;
  }
}
}


